$theme-100: #4f68f1;
$theme-200: #536dfe22;
$text-300: #585b63;
$surface-900: #f7f9fc;

footer {
 display: block;
 inline-size: 100%;
 position: relative;
 padding-block: 2.5em 1.5em;
 background: $surface-900;

 .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5em;
  inline-size: 100%;
  max-inline-size: 1200px;
  margin-inline: auto;
  padding-inline: .75em;

  & > a {
   display: flex;
   align-items: center;
   justify-content: center;
   max-inline-size: 300px;
   inline-size: 100%;

   img {
    display: block;
    object-fit: cover;
    block-size: 100%;
    inline-size: 100%;
    filter: invert(.75);
   }
  }

  ul {
   display: flex;
   align-items: center;
   justify-content: space-between;
   max-inline-size: 300px;
   inline-size: 100%;

   li {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    a {
     display: inline-flex;
     align-items: center;
     justify-content: center;
     position: relative;
     inline-size: 3em;
     block-size: 3em;
     border-radius: 50%;
     transition: background-color 150ms linear, border-color 150ms linear;
     user-select: none;
     border: 1px solid $text-300;

     svg {
      fill: $text-300;
     }

     &:hover,
     &:active {
      background-color: $theme-100;
      border-color: $theme-100;

      svg {
       fill: $surface-900;
      }
     }
    }
   }
  }

  p {
   font-size: .8em;
  }
 }
}