$theme-100: #4f68f1;
$theme-300: #4f67f1c1;
$text-400: #fcfbfb;

.filled-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 gap: .325em;
 padding: .75em 1.5em;
 font-size: 1em;
 background: $theme-100;
 color: $text-400;
 font-family: "Plus Jakarta Sans", system-ui, sans-serif;
 font-weight: 500;
 border-radius: .5em;
 user-select: none;

 &:hover,
 &:active {
  background: $theme-300;
 }
}