$theme-100: #4f68f1;
$text-200: #3c4043;
$text-300: #585b63;
$scrim-100: #00000098;
$surface-800: #ffffff;
$surface-900: #f7f9fc;
$shadow-100: 0 0 #0000,0 0 #0000,0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px -1px rgba(0,0,0,.1);

nav {
 display: block;
 inline-size: 100%;
 block-size: 3.5em;
 position: fixed;
 top: 0;
 left: 0;
 z-index: 1000;
 background: $surface-900;
 transition: box-shadow .0875s linear;

 &.shadow {
  box-shadow: $shadow-100;
 }

 .logo {
  display: inline-flex;
  align-items: center;
  gap: .25em;
  color: $text-200;
  font-family: "Plus Jakarta Sans", system-ui, sans-serif;
  position: relative;
  text-transform: uppercase;
  letter-spacing: .03em;

  strong {
   font-weight: 600;
   color: $theme-100;
  }
 }

 button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
 }

 .nav-links {
  display: none;
 }

 li {
  display: inline-flex;
  align-items: center;
  justify-content: center;
 }

 .nav-links,
 .drawer-links {
  li {
   a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: relative;
    font-family: "Plus Jakarta Sans", system-ui, sans-serif;
    font-weight: 450;
    color: var(--text-200);
    text-transform: uppercase;
    letter-spacing: .01em;

    &.active {
     color: $theme-100;
    }
   }
  }
 }

 .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  inline-size: 100%;
  block-size: 100%;
  max-inline-size: 1200px;
  margin-inline: auto;
  padding-inline: .75em;
 }

 aside {
  position: fixed;
  top: 0;
  left: -260px;
  inline-size: 260px;
  block-size: 100vh;
  overflow: hidden;
  transition: all 200ms ease 0s;
  z-index: 1000;

  &.expanded {
   left: 0px;

   .scrim {
    visibility: visible;
   }
  }

  .scrim {
   position: fixed;
   top: 0;
   left: 0;
   inline-size: 100%;
   block-size: 100%;
   overscroll-behavior: contain;
   pointer-events: inherit;
   visibility: hidden;
   z-index: 1000;
   background: $scrim-100;
   -webkit-tap-highlight-color: transparent;

   &::after {
    content: "";
    display: block;
    inline-size: 200%;
    block-size: 200%;
   }
  }

  .drawer {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 1em;
   position: relative;
   inline-size: 100%;
   block-size: 100%;
   padding-block: 1em 5em;
   z-index: 1001;
   background: $surface-800;

   .drawer-header,
   .drawer-figure,
   .drawer-links,
   .drawer-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    inline-size: 100%;
   }

   .drawer-figure {
    img {
     display: block;
     object-fit: cover;
     inline-size: 140px;
     block-size: 140px;
     border-radius: 50%;
    }
   }

   .drawer-links {
    flex-direction: column;
    justify-content: space-around;
    block-size: 100%;

    li {
     inline-size: 100%;

     a {
      inline-size: 100%;
     }
    }
   }

   .drawer-footer {
    justify-content: space-around;

    li {
     a {
      display: inline-flex;
      align-items: center;
      justify-content: center;

      svg {
       fill: $text-200;
      }

      &:hover,
      &:active {
       svg {
        fill: $theme-100;
       }
      }
     }
    }
   }
  }
 }

 @media screen and (min-width: 992px) {
  button {
   display: none;
  }

  .nav-links {
   display: flex;
   align-items: center;
   gap: 1em;
  }

  aside {
   display: none;
  }
 }
}