$text-100: #000000;
$text-400: #fcfbfb;
$border-200: #f0f1f3;

article {
 display: block;
 inline-size: 100%;
 max-inline-size: 1200px;
 margin-inline: auto;
 position: relative;
 padding-inline: .75em;

 .container {
  display: flex;
  flex-direction: column;
  inline-size: 100%;
  padding: 3em;
  border-radius: 2.5em;

  h3 {
   font-size: 1.5em;
   font-weight: 500;
   letter-spacing: .03em;
   line-height: 1.5;
   color: $text-400;
   text-shadow: 0 1px 0 $text-100;
   margin-block-end: .5em;
  }

  h4 {
   font-size: 1.25em;
   letter-spacing: .03em;
   font-weight: 500;
   color: $border-200;
   text-shadow: 0 1px 0 $text-100;
   margin-block-end: .25em;
  }

  h5 {
   font-size: 1.0875em;
   letter-spacing: .03em;
   font-weight: 500;
   color: $border-200;
   text-shadow: 0 1px 0 $text-100;
   opacity: .9;
  }

  @media screen and (max-width: 768px) {
   padding: 2em;
   border-radius: 2em;
  }
 }

 &:nth-of-type(1) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)), url("https://jeffclaybrook.s3.amazonaws.com/conferenceroom.webp") no-repeat 50% / cover;
  }
 }

 &:nth-of-type(2) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)), url("https://jeffclaybrook.s3.amazonaws.com/posed.webp") no-repeat 50% / cover;
  }
 }

 &:nth-of-type(3) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)), url("https://jeffclaybrook.s3.amazonaws.com/generic1.webp") no-repeat 50% / cover;
  }
 }

 &:nth-of-type(4) {
  .container {
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65)), url("https://jeffclaybrook.s3.amazonaws.com/generic2.webp") no-repeat 50% / cover;
  }
 }
}