$theme-100: #4f68f1;
$theme-200: #536dfe22;
$text-300: #585b63;
$surface-900: #f7f9fc;
$shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;

.about-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 inline-size: 100%;
 margin-block: 3em;

 .card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: .75em;
  text-align: center;
  border-radius: .5em;
  position: relative;
  background: $surface-900;
  box-shadow: $shadow;
  padding: 1.5em .75em;

  .card-icon {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   block-size: 3.5em;
   inline-size: 3.5em;
   padding: .75em;
   border-radius: 1em;
   background: $theme-200;

   svg {
    fill: $theme-100;
   }
  }

  h4 {
   font-size: 1.25em;
   font-weight: 500;
   letter-spacing: .02em;
   color: $text-300;
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr);
 }
}