$theme-100: #4f68f1;
$text-300: #585b63;
$border-100: #d2d3d7;
$surface-800: #ffffff;

form {
 display: flex;
 flex-direction: column;
 max-inline-size: 500px;
 inline-size: 100%;
 margin-inline: auto;
 margin-block: 3em;

 .form-control {
  display: block;
  position: relative;
  border-radius: .25em;
  inline-size: 100%;
  block-size: 3.5em;
  margin-block-end: 1em;

  input {
   position: absolute;
   top: 0;
   left: 0;
   inline-size: 100%;
   block-size: 100%;
   padding: .75em .75em .875em;
   letter-spacing: .015em;
   color: $text-300;
   background: $surface-800;
   border: 1.5px solid $border-100;
   border-radius: inherit;
   transition: all 150ms ease;
   outline: 0;
   font-family: "Plus Jakarta Sans", system-ui, sans-serif;
   font-size: .925em;

   &:focus {
    border: 1.5px solid $theme-100;

    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
     color: $theme-100;
    }
   }

   &:not(:placeholder-shown) {
    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
    }
   }
  }

  label {
   position: absolute;
   top: 1.1875em;
   left: .65em;
   padding-inline: .15rem;
   font-size: .925em;
   letter-spacing: .015em;
   background: $surface-800;
   transition: all 150ms ease;
  }
 }

 .textarea-control {
  display: block;
  position: relative;
  border-radius: .25em;
  inline-size: 100%;
  block-size: 10em;
  margin-block-end: 1.5em;

  textarea {
   position: absolute;
   top: 0;
   left: 0;
   inline-size: 100%;
   block-size: 100%;
   padding: .75em .75em .875em;
   letter-spacing: .015em;
   color: $text-300;
   background: $surface-800;
   border: 1.5px solid $border-100;
   border-radius: inherit;
   transition: all 150ms ease;
   outline: 0;
   font-family: "Plus Jakarta Sans", system-ui, sans-serif;
   font-size: .925em;
   resize: none;

   &:focus {
    border: 1.5px solid $theme-100;

    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
     color: $theme-100;
    }
   }

   &:not(:placeholder-shown) {
    & + label {
     top: -.7em;
     left: .65em;
     font-size: .7em;
    }
   }
  }

  label {
   position: absolute;
   top: 1.1875em;
   left: .65em;
   padding-inline: .15rem;
   font-size: .925em;
   letter-spacing: .015em;
   background: $surface-800;
   transition: all 150ms ease;
  }
 }
}