$theme-100: #4f68f1;
$text-300: #585b63;

section {
 display: block;
 inline-size: 100%;
 padding-block: 3em;
 position: relative;

 .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  inline-size: 100%;
  max-inline-size: 1200px;
  margin-inline: auto;
  padding-inline: .75em;

  h2 {
   font-size: 1.875em;
   font-weight: 500;
   letter-spacing: .02em;
   color: $theme-100;
   text-align: center;
   margin-block-end: .25em;
  }

  h3 {
   font-size: 1.25em;
   font-weight: 400;
   letter-spacing: .02em;
   color: $text-300;
   text-align: center;
   padding-inline: .75em;
  }
 }
}