$theme-100: #4f68f1;
$text-400: #fcfbfb;
$surface-900: #f7f9fc;

header {
 display: block;
 block-size: 100vh;
 inline-size: 100%;
 position: relative;
 background: $surface-900;

 .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  inline-size: 100%;
  block-size: 100%;
  max-inline-size: 1200px;
  margin-inline: auto;
  padding-inline: .75em;

  h1 {
   font-size: 1.75em;
   font-weight: 400;
   letter-spacing: .02em;
   margin-block-end: .125em;
   color: $theme-100;

   strong {
    font-size: 1.1875em;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: .01em;
    color: $theme-100;
   }
  }

  h2 {
   font-size: 1.125em;
   font-weight: 400;
   letter-spacing: .015em;
   margin-block-end: 1em;

   strong {
    font-weight: 600;
    color: $theme-100;
   }
  }
 }
}

.home {
 header {
  .container {
   align-items: flex-start;

   h1 {
    color: #3c4043;
    font-weight: 300;
    text-align: start;
   }
  }
 }
}