$theme-100: #4f68f1;
$theme-200: #536dfe22;
$surface-900: #f7f9fc;

.outlined-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 gap: .325em;
 text-align: center;
 padding: .75em 1.5em;
 background: transparent;
 font-size: 1em;
 color: $theme-100;
 font-family: "Plus Jakarta Sans", system-ui, sans-serif;
 font-weight: 500;
 border: 1px solid $theme-100;
 border-radius: .5em;
 user-select: none;
 transition: background-color 100ms linear;

 &:hover,
 &:active {
  background-color: $theme-100;
  color: $surface-900;

  svg {
   fill: $surface-900;
  }
 }
}