$theme-100: #4f68f1;
$theme-200: #536dfe22;
$text-100: #000000;
$text-300: #585b63;
$text-400: #fcfbfb;
$surface-900: #f7f9fc;
$shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;

.project-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 inline-size: 100%;
 margin-block: 3em;

 .card {
  display: flex;
  inline-size: 100%;
  block-size: 100%;
  border-radius: .5em;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: $shadow;
  overflow: hidden;
  transition: all 150ms ease-in-out;
  cursor: pointer;
  aspect-ratio: 5 / 3.125;
  
  &::before {
   content: "";
   position: absolute;
   inline-size: 100%;
   block-size: 100%;
   background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65));
  }

  .card-details {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   gap: .5em;
   inline-size: 100%;
   block-size: 100%;
   text-align: center;
   z-index: 10;

   h4 {
    font-size: 1.25em;
    font-weight: 500;
    letter-spacing: .02em;
    color: $text-400;
    text-shadow: 0 1px 0 $text-100;
   }

   svg {
    fill: $text-400;
   }
  }

  &:hover {
   transform: scale(1.05);
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr);
 }
}